

.toolsPanel .btn {
  min-width: 70px;
}

.bricks-grid {
  position: relative;
  background-color: gray;
  overflow: hidden;
  /* margin-bottom: 5px; */
}

.templateSector {
  position: absolute;
}

.control-panel {
  margin: 10px 0 10px;
  display: block;
  justify-content: start;
  align-items: center;
  /* border: 2px solid black; */
}

.control-panel p {
  /* margin-bottom: 10px; */
  margin: 0;
  margin-right: 5px;
}

.brick {
  position: absolute;
  box-sizing: border-box;
  top: 0;
  left: 0;
  border-color: #272f2f;
  background-color: darkgray;
  border-style: solid;
  display: inline-block;
}

.brick.active {
  background-color: lightgray !important;
}

.workArea {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.grid-area-preview {
    display: inline-grid;
}

.tileItem {
  display: block;
  position: relative;
  overflow: hidden;
}

.sectorItem {
  display: block;
  position: relative;
  overflow: hidden;
  background-color: #808080;
}

.sectorLine {
  position: absolute;
  background-color: black;
}

.active {
  background-color: aqua;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.button.brick {
  cursor: pointer;
}

.button.brick:hover {
  border-color: gray;
}

.tool-button {
  width: 20px;
  height: 20px;
  display: inline-block;
  box-sizing: border-box;
  background-size: contain;
  cursor: pointer;
}

.tool-button:hover {
  background-color: grey;
}

.trash-button {
  background-image: url(data:image/png;base64,);
}

.trash-button.buttonActive {
  background-color: lightcoral;
}

.paint-button {
  background-image: url(data:image/png;base64,);
}

.random-button {
  background-image: url(data:image/png;base64,);
}

.tool-button.buttonActive {
  border: 1px solid grey;
}
.color-list {
  display: flex;
  flex-direction: column;
  max-width: 245px;
  margin-bottom: 12px;
}

.color-list select {
  min-height: 30px;
}

.color-list label {
  margin: 0;
  margin-right: 2px;
  line-height: 2;
  /* text-align: center; */
}

.color-list .color-palette {
  height: 26px;
  overflow: auto;
  border: 2px solid gray;
  display: flex;
  flex-wrap: wrap;
}

.color-list .color-palette-item {
  width: 30px;
  height: 20px;
  text-align: center;
  border: 1px solid gray;
  margin: 1px;
  box-sizing: border-box;
}

.color-list .color-palette-item:hover {
  border: 2px solid darkslategrey;
}

 .number-input {
     -webkit-user-select: none;
        -moz-user-select: none;
         -ms-user-select: none;
             user-select: none;
    display: inline-block;
    height: 26px;
    width: 46px;
    margin: 0 6px 0 4px;
    border: 1px solid lightgray;
 }

 .number-input div {
     display: inline-block;
 }
 
 .number-input .val {
    width: 20px;
    text-align: center;
}

 .number-input .ctl {
    background-color: lightgray;
    width: 12px;
    text-align: center;
    cursor: pointer;
 }

 .number-input .ctl:hover {
    background-color: gray;
 }
/* Editor styles */

.editor-container {
  padding: 2px;
  min-height: 454px;
  display: grid;
  /* background-color: aquamarine; */
  grid-template-columns: 200px 200px 450px;
  grid-template-rows: auto;
  grid-column-gap: 10px;
  grid-row-gap: 15px;
  justify-items: center;
  align-items: center;
  grid-template-areas: 
    "tools palette tile"
    "tools workarea tile"
    "tools briks tile";
}

/* .editor-container .editor-item {
  justify-self: center;
} */

.editor-container .palette {
  grid-area: palette;
  /* background-color: whitesmoke; */
}

.editor-container .workarea {
  grid-area: workarea;
  /* background-color: green; */
}

.editor-container .briks {
  grid-area: briks;
  /* background-color: greenyellow; */

}

.editor-container .tile {
  grid-area: tile;
  /* background-color: gray; */
}

.editor-container .tools {
  grid-area: tools;
  /* background-color: gray; */
  justify-self: start;
  width: 100%;
  align-self: start;
  /* padding: 10px; */
}

/* Bricks select panel styles */

.editor-container .bricks-panel {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  width: 200px;
}

.editor-container .bricks-panel .bricks-panel-group {
  margin-bottom: 5px;
  align-self: center;
}

.editor-container .bricks-panel .bricks-panel-group:first-child {
  margin-bottom: 0;
}

/* Sector size panel styles */

 .grid-sector-options-panel {
  display: inline-block;
  text-align: right;
  margin: 0 auto;
}

 .grid-sector-options-panel .input-sector-size {
  text-align: center;
  max-width: 40px;
  padding: 0;
  margin-left: 2px;
  margin-right: 2px;
}

.color-preview .color {
  width: 60px;
  /* position: relative;
  top: -4px; */
